import React, { useState, useEffect, useRef } from 'react';
import { Select, Space, Table, Input, Button, Drawer, Card, Divider, Progress, message } from 'antd';
import { WarningTwoTone } from '@ant-design/icons';

var details = [];
var matchedResults = [];
function Search() {
    // 
    const [messageApi, contextHolder] = message.useMessage();
    const success = () => {
        messageApi.open({
            type: 'success',
            content: '',
        });
    };
    // Columns
    const columns = [
        {
            title: '',
            dataIndex: 's_pic',
            key: 's_pic',
        },
        {
            title: '',
            dataIndex: 's_name',
            key: 's_name',
        },
        {
            title: '',
            dataIndex: 's_sname',
            key: 's_sname',
        },
        {
            title: 'id',
            dataIndex: 's_id',
            key: 's_id',
        },
        {
            title: 'Action',
            key: 'action',
            render: (_, record) => (
                <Space size="middle" onClick={() => handleDetailClick(record.s_id)} key={record.s_id}>
                    <a></a>
                </Space>
            ),
        },
    ];

    // Drawer
    const [open, setOpen] = useState(false);
    const [drawerinfo, setDrawerinfo] = useState("");
    const showDrawer = () => {
        setOpen(true);
    };
    const onClose = () => {
        setOpen(false);
        pauseAudio();
    };

    // Aplayer-react
    const audioRef = useRef(null);
    const pauseAudio = () => {
        if (audioRef.current) {
            audioRef.current.pause();
        }
    };

    const [contentList, setContentlist] = useState({
        tab1: <p key="tab1"> tab1</p>,
        tab2: <p key="tab2"> tab2</p>,
        tab3: <p key="tab3"> tab3</p>,
    });
    // Tabs
    const tabList = [
        {
            key: 'tab1',
            tab: '',
        },
        {
            key: 'tab2',
            tab: 'TTML',
        },
        {
            key: 'tab3',
            tab: '',
        },
    ];
    const [activeTabKey1, setActiveTabKey1] = useState('tab1');
    // Tabs
    const onTab1Change = (key) => {
        setActiveTabKey1(key);
    };

    // 
    const handleDetailClick = (s_id) => {
        console.log(',id:', s_id);
        const matchedDetail = details.find(detail => detail.s_id === s_id);
        console.log(":", matchedDetail);
        setDrawerinfo([
            <p key={matchedDetail.s_pic}><img src={matchedDetail.s_pic} width="80vh" /></p>,
            <p key={matchedDetail.s_id}>id:{matchedDetail.s_id}</p>,
            <p key={matchedDetail.s_name}>:{matchedDetail.s_name}</p>,
            <p key={matchedDetail.s_sname}>:{matchedDetail.s_sname}</p>,
            <p key={matchedDetail.s_downurl}><audio src={matchedDetail.s_downurl} ref={audioRef} controls /></p>,
        ])
        setContentlist(
            {
                tab1: <p key="tab1"><WarningTwoTone twoToneColor="#eb2f96" />
                    &nbsp;,
                    <br /><br />
                    <a href={matchedDetail.s_downurl} target='_blank'></a>
                </p>,
                tab2: <p key="tab2"><WarningTwoTone twoToneColor="#eb2f96" />
                    &nbsp;,
                    <br /><br />
                    <a href={matchedDetail.ttml_downurl} target='_blank'>TTML</a>
                    <br />
                    <a href={matchedDetail.ttml_url} target='_blank'>Github</a>
                </p>,
                tab3: <p key="tab3"><WarningTwoTone twoToneColor="#eb2f96" />
                    &nbsp;,
                    <br /><br />
                    <a href={"https://music.163.com/#/song?id=" + matchedDetail.s_id} target='_blank'></a>
                </p>,
            }
        );
        showDrawer();
    };
    //  localStorage 
    var localdata = [];
    var storedMusicDataString = localStorage.getItem('amlldata');
    if (storedMusicDataString == null || storedMusicDataString == 'null' || storedMusicDataString == '') {
        storedMusicDataString = " { \"time_ver\": \"\" } "
        var localdata = [];
    } else {
        // 
        const storedMusicData = JSON.parse(storedMusicDataString);
        details = storedMusicData;
        var localdata = [];
        storedMusicData.map(item => (
            // mapReact
            localdata.push({
                key: item.s_id,
                s_pic: <img src={item.s_pic} width='40vh' />,
                s_name: item.s_name,
                s_sname: item.s_sname,
                s_id: item.s_id,
                action: item.s_id
            })
        ))
    }
    const [data, setData] = useState(localdata);
    const [searchdata, setSearchdata] = useState(localdata);
    useEffect(() => {
        const handleStorageChange = () => {
            //  localStorage 
            var upddata = [];
            var storedMusicDataString = localStorage.getItem('amlldata');
            const storedMusicData = JSON.parse(storedMusicDataString);
            storedMusicData.map(item => (
                // mapReact
                upddata.push({
                    key: item.s_id,
                    s_pic: <img src={item.s_pic} width='40vh' />,
                    s_name: item.s_name,
                    s_sname: item.s_sname,
                    s_id: item.s_id,
                    action: item.s_id
                })
            ))
            setData(upddata);
            setSearchdata(upddata);
        };
        // 
        window.addEventListener('localstorageDataChanged', handleStorageChange);
        // 
        return () => {
            window.removeEventListener('localstorageDataChanged', handleStorageChange);
        };
    }, [data]); // 

    // name
    function fuzzyMatchName(input, data) {
        const regex = new RegExp(input, 'i'); // 'i' 
        return data.filter(item => regex.test(item.s_name));//
    }
    // id
    function fuzzyMatchId(input, data) {
        const regex = new RegExp(input, 'i'); // 'i' 
        return data.filter(item => regex.test(item.s_id));//id
    }

    // 
    const [selectedOption, setSelectedOption] = useState('name');
    const [inputValue, setInputValue] = useState('');

    // 
    const handleChange = (value) => {
        setSelectedOption(value);
    };

    // Progress + buttonDisabled
    const [progressVisible, setProgressVisible] = useState(true);
    const [progressPercent, setProgressPercent] = useState(0);
    const [progressHint, setProgressHint] = useState("");
    const [button_disabled, setButtondisabled] = useState(false);

    // 
    const handleSearch = () => {
        //button
        setButtondisabled(true);
        // Progress
        //setProgressVisible(true);
        setProgressPercent(10);
        setProgressHint("");
        // 
        console.log(':', selectedOption);
        console.log(':', inputValue);
        if (selectedOption == "name") {
            setProgressPercent(50);
            setProgressHint("");
            console.log(":", searchdata);
            matchedResults = fuzzyMatchName(inputValue, searchdata);
            console.log("name:", matchedResults);
            setProgressPercent(80);
            setProgressHint("");
            setData(matchedResults);
        } else {
            setProgressPercent(50);
            setProgressHint("");
            console.log(":", searchdata);
            matchedResults = fuzzyMatchId(inputValue, searchdata);
            console.log("id:", matchedResults);
            setProgressPercent(80);
            setProgressHint("");
            setData(matchedResults);
        }
        // 
        setProgressPercent(100);
        setProgressHint("");
        //setProgressVisible(false);
        setButtondisabled(false);
        success();
        function initProgress(){
            setProgressPercent(0);
            setProgressHint("");
            console.log("Progress")
        }
        setTimeout(initProgress, 3000);
    };
    return (
        <>
            {contextHolder}
            <Space.Compact style={{ width: '50%' }}>
                <Select
                    defaultValue="name"
                    style={{
                        width: 320,
                    }}
                    onChange={handleChange}
                    options={[
                        {
                            value: 'name',
                            label: '',
                        },
                        {
                            value: 'id',
                            label: 'id',
                        },
                    ]}
                />
                <Input placeholder="id/" onChange={(e) => setInputValue(e.target.value)} />
                <Button type="primary" onClick={handleSearch} disabled={button_disabled}></Button>
                {progressVisible && (
                    <Button type="text">
                        <Progress
                            type="circle"
                            trailColor="#e6f4ff"
                            percent={progressPercent}
                            strokeWidth={20}
                            size={14}
                            format={(number) => `${number}%`}
                        />
                        <span
                            style={{
                                marginLeft: 8,
                            }}
                        >
                            {progressHint}
                        </span>
                    </Button>
                )}
            </Space.Compact>
            <Divider />
            <Table columns={columns} dataSource={data} size='middle' pagination={{ pageSize: 7, showSizeChanger: false, showQuickJumper: true }} />
            <Drawer title="" placement="right" onClose={onClose} open={open}>
                {drawerinfo}
                <Card style={{ width: '100%', }} title="" tabList={tabList} activeTabKey={activeTabKey1} onTabChange={onTab1Change} tabProps={{ size: 'middle', }} hoverable={true}>{contentList[activeTabKey1]}</Card>
            </Drawer>
        </>
    );
}

export default Search;