import React, { useState, useEffect, useRef } from 'react';
import { Space, Table, Drawer, Card } from 'antd';
import { WarningTwoTone } from '@ant-design/icons';
/*
[
{
    "s_id": "1308359582",
    "s_name": "",
    "s_sname": "",
    "s_pic": "https://p2.music.126.net/idpBKqSHCDfEw8RAofHWbQ==/109951163540042089.jpg",
    "s_downurl": "https://m7.music.126.net/20231223143708/87d3da1f3652b888f41443c00b81549b/ymusic/b346/23de/5e90/3c2f22e190b784d84adc1b6c3ebcaf35.mp3",
    "ttml_url": "https://github.com/Steve-xmh/amll-ttml-db/blob/main/lyrics/1308359582.ttml",
    "ttml_downurl": "https://raw.githubusercontent.com/Steve-xmh/amll-ttml-db/main/lyrics/1308359582.ttml"
}
]
*/

//  
var details = [];

function Database() {

    // Columns
    const columns = [
        {
            title: '',
            dataIndex: 's_pic',
            key: 's_pic',
        },
        {
            title: '',
            dataIndex: 's_name',
            key: 's_name',
        },
        {
            title: '',
            dataIndex: 's_sname',
            key: 's_sname',
        },
        {
            title: 'id',
            dataIndex: 's_id',
            key: 's_id',
        },
        {
            title: 'Action',
            key: 'action',
            render: (_, record) => (
                <Space size="middle" onClick={() => handleDetailClick(record.s_id)} key={record.s_id}>
                    <a></a>
                </Space>
            ),
        },
    ];

    // Drawer
    const [open, setOpen] = useState(false);
    const [drawerinfo, setDrawerinfo] = useState("");
    const showDrawer = () => {
        setOpen(true);
    };
    const onClose = () => {
        setOpen(false);
        pauseAudio();
    };

    // Mute audio
    const audioRef = useRef(null);
    const pauseAudio = () => {
        if (audioRef.current) {
            audioRef.current.pause();
        }
    };

    const [contentList, setContentlist] = useState({
        tab1: <p key="tab1"> tab1</p>,
        tab2: <p key="tab2"> tab2</p>,
        tab3: <p key="tab3"> tab3</p>,
    });
    // Tabs
    const tabList = [
        {
            key: 'tab1',
            tab: '',
        },
        {
            key: 'tab2',
            tab: 'TTML',
        },
        {
            key: 'tab3',
            tab: '',
        },
    ];
    const [activeTabKey1, setActiveTabKey1] = useState('tab1');
    // Tabs
    const onTab1Change = (key) => {
        setActiveTabKey1(key);
    };

    // 
    const handleDetailClick = (s_id) => {
        console.log(',id:', s_id);
        const matchedDetail = details.find(detail => detail.s_id === s_id);
        console.log(":", matchedDetail);
        setDrawerinfo([
            <p key={matchedDetail.s_pic}><img src={matchedDetail.s_pic} width="80vh" /></p>,
            <p key={matchedDetail.s_id}>id:{matchedDetail.s_id}</p>,
            <p key={matchedDetail.s_name}>:{matchedDetail.s_name}</p>,
            <p key={matchedDetail.s_sname}>:{matchedDetail.s_sname}</p>,
            <p key={matchedDetail.s_downurl}><audio src={matchedDetail.s_downurl} ref={audioRef} controls /></p>,
        ])
        setContentlist(
            {
                tab1: <p key="tab1"><WarningTwoTone twoToneColor="#eb2f96"/>
                    &nbsp;,
                    <br /><br />
                    <a href={matchedDetail.s_downurl} target='_blank'></a>
                </p>,
                tab2: <p key="tab2"><WarningTwoTone twoToneColor="#eb2f96"/>
                    &nbsp;,
                    <br /><br />
                    <a href={matchedDetail.ttml_downurl} target='_blank'>TTML</a>
                    <br />
                    <a href={matchedDetail.ttml_url} target='_blank'>Github</a>
                </p>,
                tab3: <p key="tab3"><WarningTwoTone twoToneColor="#eb2f96"/>
                    &nbsp;,
                    <br /><br />
                    <a href={"https://music.163.com/#/song?id=" + matchedDetail.s_id} target='_blank'></a>
                </p>,
            }
        );
        showDrawer();
    };

    //  localStorage 
    var localdata = [];
    var storedMusicDataString = localStorage.getItem('amlldata');
    if (storedMusicDataString == null || storedMusicDataString == 'null' || storedMusicDataString == '') {
        storedMusicDataString = " { \"time_ver\": \"\" } "
        var localdata = [];
    } else {
        // 
        const storedMusicData = JSON.parse(storedMusicDataString);
        details = storedMusicData;
        var localdata = [];
        storedMusicData.map(item => (
            // mapReact
            localdata.push({
                key: item.s_id,
                s_pic: <img src={item.s_pic} width='40vh' />,
                s_name: item.s_name,
                s_sname: item.s_sname,
                s_id: item.s_id,
                action: item.s_id
            })
        ))
    }
    const [data, setData] = useState(localdata);
    useEffect(() => {
        const handleStorageChange = () => {
            //  localStorage 
            var upddata = [];
            var storedMusicDataString = localStorage.getItem('amlldata');
            const storedMusicData = JSON.parse(storedMusicDataString);
            storedMusicData.map(item => (
                // mapReact
                upddata.push({
                    key: item.s_id,
                    s_pic: <img src={item.s_pic} width='40vh' />,
                    s_name: item.s_name,
                    s_sname: item.s_sname,
                    s_id: item.s_id,
                    action: item.s_id
                })
            ))
            setData(upddata);
        };
        // 
        window.addEventListener('localstorageDataChanged', handleStorageChange);
        // 
        return () => {
            window.removeEventListener('localstorageDataChanged', handleStorageChange);
        };
    }, [data]); // 

    return (
        <>
            <Table columns={columns} dataSource={data} size='middle' pagination={{ pageSize: 8, showSizeChanger: false, showQuickJumper: true }} />
            <Drawer title="" placement="right" onClose={onClose} open={open}>
                {drawerinfo}
                <Card style={{ width: '100%', }} title="" tabList={tabList} activeTabKey={activeTabKey1} onTabChange={onTab1Change} tabProps={{ size: 'middle', }} hoverable={true}>{contentList[activeTabKey1]}</Card>
            </Drawer>
        </>
    );
}

export default Database;