import React, { useState, useEffect } from 'react';
import Localdb from "./components/localdb";
import {
  MenuFoldOutlined,
  MenuUnfoldOutlined,
  DatabaseOutlined,
  HomeOutlined,
  SearchOutlined,
  ToolOutlined,
} from '@ant-design/icons';
import { Layout, Menu, Button, theme, notification } from 'antd';
import { useNavigate, Outlet, useLocation } from 'react-router-dom';
const { Header, Sider, Content, Footer } = Layout;
function App() {
  const [collapsed, setCollapsed] = useState(false);
  const {
    token: { colorBgContainer, borderRadiusLG },
  } = theme.useToken();
  const contentStyle = {
    margin: '24px 16px',
    padding: 24,
    minHeight: 280,
    background: colorBgContainer,
    borderRadius: borderRadiusLG,
  }

  // 
  const navigate = useNavigate();
  const onMenuClick = (route) => {
    const path = route.key;
    navigate(path);
  }
  const location = useLocation();
  const selectedKey = location.pathname;

  //
  const [api, contextHolder] = notification.useNotification();
  const openNotification = () => {
    api.error({
      message: '',
      description:
        ', ',
      duration: 0,
    });
  };

  // 
  var i = 0;
  useEffect(() => {
    function isMobileDevice() {
      console.log("UA ");
      var UAstatus = /Android|webOS|iPhone|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
      console.log("UA ", UAstatus);
      if (UAstatus == true && i == 0) {
        console.log("UA  ");
        openNotification();
        i++;
      }
    }
    isMobileDevice();
  }, []);

  return (
    <div className="App">
      {contextHolder}
      <Layout>
        <Sider trigger={null} collapsible collapsed={collapsed}>
          <div className="demo-logo-vertical" />
          <Menu
            theme="dark"
            mode="inline"
            selectedKeys={selectedKey}
            items={[
              {
                key: '/search-in-amlldb',
                icon: <HomeOutlined />,
                label: '',
              },
              {
                key: '/search-in-amlldb/search',
                icon: <SearchOutlined />,
                label: '',
              },
              {
                key: '/search-in-amlldb/database',
                icon: <DatabaseOutlined />,
                label: '',
              },
              {
                key: '/search-in-amlldb/ttml-tool',
                icon: <ToolOutlined />,
                label: 'TTML',
              },
            ]}
            onClick={onMenuClick}
          />
        </Sider>
        <Layout
          style={{
            minHeight: '100vh',
            display: 'flex',
          }}
        >
          <Header
            style={{
              padding: 0,
              background: colorBgContainer,
            }}
          >
            <Button
              type="text"
              icon={collapsed ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
              onClick={() => setCollapsed(!collapsed)}
              style={{
                fontSize: '16px',
                width: 64,
                height: 64,
              }}
            />
            <Localdb />
          </Header>
          <Content style={contentStyle}>
            {/*  */}
            <Outlet />
          </Content>
          <Footer
            style={{
              textAlign: 'center',
            }}
          >
            Search In AMLL-DB@0.3.0 React
          </Footer>
        </Layout>
      </Layout>
    </div>
  );
}

export default App;
